"use client";
import { useState, useEffect } from "react";
import { BiSearch, BiTrendingUp } from "react-icons/bi";
import { FiCheckCircle } from "react-icons/fi";
import useAdminStore from "@/store/adminStore";
import toast from "react-hot-toast";

export default function FundraisedDataTable() {
  const { campaigns, isLoading, getAllCampaigns } = useAdminStore();
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    const fetchCompleted = async () => {
      try {
        // Fetch campaigns - backend limit is max 100
        // We'll fetch first page with max limit to get most campaigns
        // For completed campaigns, 100 should be enough for most cases
        await getAllCampaigns({ limit: 100, page: 1 });
      } catch (error) {
        console.error("Failed to load completed campaigns:", error);
        toast.error("Failed to load data");
      }
    };
    fetchCompleted();
  }, [getAllCampaigns]);

  // Debug: Log campaigns when they change
  useEffect(() => {
    if (campaigns.length > 0) {
      console.log("Campaigns loaded:", campaigns.length);
      const completed = campaigns.filter(c => c.status === 'completed' || (c.currentAmount >= c.goalAmount && c.goalAmount > 0));
      console.log("Completed campaigns found:", completed.length);
    }
  }, [campaigns]);

  // Filter for completed campaigns
  const completedCampaigns = campaigns.filter(campaign => {
    // A campaign is completed if:
    // 1. status is 'completed', OR
    // 2. currentAmount >= goalAmount (goal reached), OR
    // 3. End date has passed
    const now = new Date();
    const endDate = campaign.timeline?.endDate ? new Date(campaign.timeline.endDate) : null;
    const goalReached = campaign.goalAmount > 0 && campaign.currentAmount >= campaign.goalAmount;
    const isExpired = endDate && endDate < now;
    
    const isCompleted = campaign.status === 'completed' || 
           goalReached ||
           (isExpired && campaign.status === 'active');
    
    return isCompleted;
  });

  console.log("=== Fundraised Data Debug ===");
  console.log("Total campaigns:", campaigns.length);
  console.log("Completed campaigns:", completedCampaigns.length);
  console.log("Campaign statuses:", [...new Set(campaigns.map(c => c.status))]);
  console.log("Sample campaigns:", campaigns.slice(0, 3).map(c => ({
    title: c.title,
    status: c.status,
    currentAmount: c.currentAmount,
    goalAmount: c.goalAmount,
    goalReached: c.goalAmount > 0 && c.currentAmount >= c.goalAmount
  })));

  const filteredFundraised = completedCampaigns.filter(item =>
    item.title?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    item.fundraiser?.name?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const indexOfLastEntry = currentPage * entriesPerPage;
  const indexOfFirstEntry = indexOfLastEntry - entriesPerPage;
  const currentEntries = filteredFundraised.slice(indexOfFirstEntry, indexOfLastEntry);
  const totalPages = Math.ceil(filteredFundraised.length / entriesPerPage);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Completed FundRaised Data</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li>
              <a href="/admin" className="hover:underline text-gray-600 hover:text-blue-600">
                Home
              </a>
            </li>
            <li className="text-gray-400">/</li>
            <li>
              <a href="/admin/fundraiser-live" className="hover:underline text-gray-600 hover:text-blue-600">
                FundRaiser Live
              </a>
            </li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Completed FundRaiser</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-2">
            <select
              value={entriesPerPage}
              onChange={(e) => {
                setEntriesPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
            <span className="text-sm text-green-600 font-medium">entries per page</span>
          </div>

          <div className="relative">
            <input
              type="text"
              placeholder="Search..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setCurrentPage(1);
              }}
              className="border border-gray-300 rounded-lg pl-4 pr-10 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 w-64"
            />
            <BiSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
          </div>
        </div>

        {isLoading ? (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500 mx-auto"></div>
            <p className="text-gray-600 mt-4">Loading completed campaigns...</p>
          </div>
        ) : (
          <>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="bg-gradient-to-r from-green-50 to-blue-50 border-b-2 border-green-200">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Campaign Title</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Goal Amount</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Raised Amount</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">% Achieved</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Duration</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Fundraiser</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {currentEntries.length > 0 ? (
                    currentEntries.map((campaign, index) => {
                      const percentage = campaign.goalAmount > 0 
                        ? ((campaign.currentAmount / campaign.goalAmount) * 100).toFixed(1)
                        : '100';
                      return (
                        <tr key={campaign._id} className={`border-b hover:bg-gray-50 transition-colors ${index % 2 === 0 ? 'bg-white' : 'bg-gray-50/30'}`}>
                          <td className="px-4 py-4">
                            <div className="font-medium text-gray-900">{campaign.title}</div>
                            <div className="text-xs text-gray-500">{campaign.category}</div>
                          </td>
                          <td className="px-4 py-4 text-sm font-medium text-gray-900">
                            â‚¹{campaign.goalAmount?.toLocaleString() || 0}
                          </td>
                          <td className="px-4 py-4 text-sm font-medium text-green-600">
                            â‚¹{campaign.currentAmount?.toLocaleString() || 0}
                          </td>
                          <td className="px-4 py-4">
                            <div className="flex items-center gap-2">
                              <div className="flex-1 bg-gray-200 rounded-full h-2 max-w-[100px]">
                                <div 
                                  className="bg-green-500 h-2 rounded-full transition-all duration-300"
                                  style={{ width: `${Math.min(percentage, 100)}%` }}
                                ></div>
                              </div>
                              <span className="text-sm font-medium text-gray-900">{percentage}%</span>
                            </div>
                          </td>
                          <td className="px-4 py-4 text-sm text-gray-700">
                            {campaign.timeline?.startDate ? (
                              <>
                                <div>{new Date(campaign.timeline.startDate).toLocaleDateString()}</div>
                                {campaign.timeline?.endDate && (
                                  <div className="text-xs text-gray-500">to {new Date(campaign.timeline.endDate).toLocaleDateString()}</div>
                                )}
                              </>
                            ) : (
                              <span className="text-gray-400">N/A</span>
                            )}
                          </td>
                          <td className="px-4 py-4 text-sm">
                            <div className="text-gray-900">{campaign.fundraiser?.name || 'N/A'}</div>
                            <div className="text-xs text-gray-600">
                              {campaign.fundraiser?.phone || campaign.beneficiary?.contact?.phone || 'N/A'}
                            </div>
                            {campaign.fundraiser?.email && (
                              <div className="text-xs text-gray-500">{campaign.fundraiser.email}</div>
                            )}
                          </td>
                          <td className="px-4 py-4">
                            <span className="px-3 py-1 rounded-full text-xs font-medium bg-green-100 text-green-700 flex items-center gap-1 w-fit">
                              <FiCheckCircle size={14} />
                              Completed
                            </span>
                          </td>
                        </tr>
                      );
                    })
                  ) : (
                    <tr>
                      <td colSpan="7" className="px-4 py-12 text-center text-gray-500">
                        <div className="text-4xl mb-4">ðŸŽ‰</div>
                        {isLoading ? (
                          <p className="text-lg font-medium">Loading completed campaigns...</p>
                        ) : (
                          <div>
                            <p className="text-lg font-medium mb-2">No completed fundraisers yet</p>
                            <div className="text-sm text-gray-400 mt-4 max-w-md mx-auto space-y-2">
                              <p className="font-semibold text-gray-600 mb-3">A campaign appears here when:</p>
                              <ul className="list-disc list-inside space-y-1 text-left">
                                <li>Status is manually set to <span className="font-mono text-blue-600">'completed'</span></li>
                                <li>Goal amount is reached (Raised Amount â‰¥ Goal Amount)</li>
                                <li>End date has passed and campaign was active</li>
                              </ul>
                              <div className="mt-4 p-3 bg-blue-50 rounded-lg border border-blue-200">
                                <p className="text-xs text-blue-800">
                                  <strong>Note:</strong> Currently, all campaigns are either <span className="font-mono">'pending'</span> (waiting for approval) or <span className="font-mono">'active'</span> (running). 
                                  Once a campaign reaches its goal or expires, it will automatically appear here.
                                </p>
                              </div>
                            </div>
                          </div>
                        )}
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </>
        )}
      </div>
    </>
  );
}



